export default class GL {
  /**
   * @classdesc GL
   * @constructor GL
   * @author Zhoyq <feedback@zhoyq.com>
   * @since 2021-01-10
   * @param {App} app 
   */
  constructor(app) {
    const self = this;

    self._gl = app._renderer.context;
  }

  /**
   * 
   * @memberof GL#
   * @method getSupportedExtensions
   */
  async getSupportedExtensions() {
    return this._gl.getSupportedExtensions();
  }

  /**
   *  gl 
   * @memberof GL#
   * @method getParameter
   * @param {number} queryParameter KEY
   */
  async getParameter(queryParameter) {}

  /**
   *  gl 
   * @memberof GL#
   * @method getParameters
   * @param {Array} queryParameters KEY
   */
  async getParameters(queryParameters) {}
}
