import Scene from './Scene.js';

export default class Context {
  /**
   * @classdesc
   * 
   *
   * ****
   *
   * - getLevelsetLevelsetLevels
   * - getCurrentScene
   *
   * @constructor Context
   * @author Zhoyq <feedback@zhoyq.com>
   * @since 2021-01-10
   * @param {App} app 
   */
  constructor(app) {
    const self = this;

    //  app
    self._app = app;

    // 
    self._sceneMap = new Map();

    const { _options } = self._app;
    const { definition } = _options;
    const { scenes, scene } = definition;

    self.load(scenes);
    // 
    self._currentScene = self._sceneMap.get(scene);
  }

  /**
   * 
   * @memberof App#
   * @method load
   * @param {object} scenes 
   *
   * ```json
   * {
   *   'sceneid': {
   *      url:''
   *      parameter: {}
   *   }
   * }
   * ```
   */
  load(scenes) {
    const self = this;
    const sceneKeys = Object.keys(scenes);

    for (let i = 0; i < sceneKeys.length; i++) {
      self._sceneMap.set(sceneKeys[i], new Scene(scenes[sceneKeys[i]]));
    }
  }

  /**
   * 
   *
   * ```js
   * // 
   * const level1 = NOTHING.Level(scene1);
   * const level = NOTHING.Level(scene, [level1]);
   * app.getContext().setLevels(level);
   * ```
   *
   * @memberof Context#
   * @method setLevels
   * @param {Level} level 
   */
  setLevels(level) {}

  /**
   * 
   * @memberof Context#
   * @method setLevel
   * @param {Level} level 
   */
  setLevel(level) {}

  /**
   * 
   * @memberof Context#
   * @method setLevel
   * @returns {Level} 
   */
  getLevel() {}

  /**
   * 
   * @memberof Context#
   * @method getCurrentScene
   * @param {Scene} 
   */
  getCurrentScene() {
    return this._currentScene;
  }
}
